export default {
	name: "tuiSwipeAction",
	props: {
		// name: '删除',
		// color: '#fff',
		// fontsize: 32,//单位upx
		// width: 80, //单位px
		// icon: 'like.png',//此处为图片地址
		// background: '#ed3f14'
		actions: {
			type: Array,
			default: function(){
				return []
			}
		},
		//是否可关闭，默认可关闭，可以单独控制
		closable: {
			type: Boolean,
			default: true
		},
		//设为false，可以滑动多行不关闭菜单
		showMask: {
			type: Boolean,
			default: true
		},
		operateWidth: {
			type: Number,
			default: 80
		},
		params: {
			type: Object,
			default: function(){
				return {}
			}
		}
	},
	watch: {
		actions(newValue, oldValue) {
			this.updateButtonSize()
		}
	},
	data() {
		return {
			//start position
			tStart: {
				pageX: 0,
				pageY: 0
			},
			//限制滑动距离
			limitMove: 0,
			//move position
			position: {
				pageX: 0,
				pageY: 0
			},
			isShowBtn: false
		};
	},





	onReady() {
		this.updateButtonSize()
	},
	methods: {
		swipeDirection(x1, x2, y1, y2) {
			return Math.abs(x1 - x2) >=
				Math.abs(y1 - y2) ? (x1 - x2 > 0 ? 'Left' : 'Right') : (y1 - y2 > 0 ? 'Up' : 'Down')
		},
		//阻止事件冒泡
		loop() {},
		updateButtonSize() {
			const actions = this.actions;
			if (actions.length > 0) {
				const query = uni.createSelectorQuery().in(this);
				let limitMovePosition = 0;
				actions.forEach(item => {
					limitMovePosition += item.width || 0;
				});
				this.limitMove = limitMovePosition;
			} else {
				this.limitMove = this.operateWidth;
			}
		},
		handlerTouchstart(event) {
			const touches = event.touches ? event.touches[0] : {};
			const tStart = this.tStart;
			if (touches) {
				for (let i in tStart) {
					if (touches[i]) {
						tStart[i] = touches[i];
					}
				}
			}
		},
		swipper(touches) {
			const start = this.tStart;
			const spacing = {
				pageX: touches.pageX - start.pageX,
				pageY: touches.pageY - start.pageY
			}
			if (this.limitMove < Math.abs(spacing.pageX)) {
				spacing.pageX = -this.limitMove;

			}
			this.position = spacing
		},
		handlerTouchmove(event) {
			const start = this.tStart;
			const touches = event.touches ? event.touches[0] : {};
			if (touches) {
				const direction = this.swipeDirection(start.pageX, touches.pageX, start.pageY, touches.pageY);
				if (direction === 'Left') {
					this.swipper(touches);
				}
			}
		},
		handlerTouchend(event) {
			const start = this.tStart;
			const touches = event.changedTouches ? event.changedTouches[0] : {};
			if (touches) {
				const direction = this.swipeDirection(start.pageX, touches.pageX, start.pageY, touches.pageY);
				const spacing = {
					pageX: touches.pageX - start.pageX,
					pageY: touches.pageY - start.pageY
				}
				if (Math.abs(spacing.pageX) >= 40 && direction === "Left") {
					spacing.pageX = spacing.pageX < 0 ? -this.limitMove : this.limitMove;
					this.isShowBtn = true
				} else {
					spacing.pageX = 0;
				}
				this.position = spacing
			}
		},
		handlerButton(event) {
			if (this.closable) {
				this.closeButtonGroup();
			}
			const dataset = event.currentTarget.dataset;
			this.$emit('click', {
				index: Number(dataset.index),
				item: this.params
			})
			
		},
		closeButtonGroup() {
			this.position = {
				pageX: 0,
				pageY: 0
			};
			this.isShowBtn = false
		},
		//控制自定义组件
		handlerParentButton(event) {
			if (this.closable) {
				this.closeButtonGroup();
			}
		},
		px(num) {
			return uni.upx2px(num) + "px"
		}
	}
}
